.pagination {
  justify-content: center;
  margin: 0;
  padding: 0;
  display: flex;
}

.pagination button {
  color: #111;
  text-align: center;
  cursor: pointer;
  background-color: #d0d0d0;
  border: 1px solid #111;
  border-radius: 50%;
  margin: 5px;
  padding: 11px 17px;
  font-size: 18px;
  font-weight: 700;
  text-decoration: none;
  display: flex;
}

.pagination button.number:hover, .pagination button.number:focus {
  color: #fff;
  background-color: #111;
}

.pagination button.double-arrow {
  color: #fff;
  background-color: #111;
  border-radius: 50%;
  margin-right: 19px;
  padding: 15px 18px;
  font-size: 18px;
  font-weight: 700;
}

.pagination button.double-doublearrow {
  color: #fff;
  background-color: #111;
  border-radius: 50%;
  margin-right: 8px;
  padding: 15px 18px;
  font-size: 18px;
  font-weight: 700;
}

.pagination button.double-arrow-next {
  color: #111;
  background-color: #eac645;
  border-radius: 50%;
  margin-left: 19px;
  padding: 15px 18px;
  font-size: 18px;
  font-weight: 700;
}

.pagination button.double-arrow-doublenext {
  color: #111;
  background-color: #eac645;
  border-radius: 50%;
  margin-left: 8px;
  padding: 15px 18px;
  font-size: 18px;
  font-weight: 700;
}

